// Import styles (main.scss)
import '@/assets/scss/main.scss'

// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store/store'
import VeeValidate from 'vee-validate'
import VueAwesomeSwiper from 'vue-awesome-swiper'
import VModal from 'vue-js-modal'
import Axios from 'axios'
import ClipLoader from 'vue-spinner/src/ClipLoader.vue'
// require swiper styles
import 'swiper/dist/css/swiper.css'

import VueBlu from 'vue-blu'
// import 'vue-blu/dist/css/vue-blu.min.css'

Vue.prototype.$axios = Axios
Vue.config.productionTip = false
Vue.use(VeeValidate);
Vue.use(VueAwesomeSwiper);
Vue.use(VueBlu);
Vue.use(VModal);
Vue.component('clip-loader', ClipLoader);

// creating a global reactive variable to keep track of steps in all component instance
// TODO: use vuex for this
let globalData = new Vue({
  data: {
    $step: 0
  }
});
Vue.mixin({
  computed: {
    $step: {
      get: function () {
        return globalData.$data.$step;
      },
      set: function (newstep) {
        globalData.$data.$step = newstep;
      }
    }
  }
});

/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  router,
  components: {
    App
  },
  template: '<App/>'
})



