<template>
    <div id="lifeQuize" class="lifeQuize">
        <header class="site-header">
            <a href="" class="site-url"><img src="@/assets/images/logo.png" alt="" class="logo"></a>
          <ul class="progress">
              <div class="progress-bar" :style="{width:$step*25+'%'}"></div>
              <div class="progress-bar-base"></div>
              <li class="progress-step" :class="{active:step==0,done:step>0}">
                <span class="progress-step-serial">1
                  <span class="progress-step-name">Accomplishments</span>
                </span>
                
              </li>
              <li class="progress-step" :class="{active:step==1,done:step>1}">
                <span class="progress-step-serial">2
                  <span class="progress-step-name">Set future goals</span>
                </span>
                
              </li>
              <li class="progress-step" :class="{active:step==2,done:step>2}">
                <span class="progress-step-serial">3
                  <span class="progress-step-name">Assess risks</span>
                </span>
                
              </li>
              <li class="progress-step" :class="{active:step==3,done:step>3}">
                <span class="progress-step-serial">4
                  <span class="progress-step-name">Save my plan</span>
                </span>
                
              </li>
            </ul>
        </header>

        <div class="lifeQuize__main">
            <div class="futureGoals" id="futureGoals">
                <h2 class="message">Now, let's talk about your future</h2>
                <p class="direction">Take a few minutes to think about your future goals. Select a few options to represent what you want to achieve.</p>
                <ul class="categories container-fluid">
                  <div v-for="(category,categoryId) in categories" :key="categoryId" class="category col-xs-12 col-sm-6 col-lg-3 col-xl-3" :class="category.class">
                      <p class="category__title">{{category.name}}</p>
                      <ul class="category__options">
                          <li v-for="(option,optionId) in category.options" :key="optionId" class="category__option" @click="selectNewGoal(categoryId,optionId)" :disabled="option.selected">
                            <span>{{option.titleFuture}}</span>
                          </li>
                      </ul>
                  </div>

                  <div class="category addNew col-xs-12 col-sm-6 col-lg-3 col-xl-3">
                    <p class="category__title">Add new goal</p>
                    <div class="newOtherGoals">
                      <p v-for="(goal,id) in newOtherGoals" :key="id" class="newOtherGoal">
                        {{goal.titleFuture}}
                        <button @click="removeNewOtherGoal(id,goal.catId,goal.optId)"><i class="fas fa-times"></i></button>
                      </p>
                    </div>
                    <div class="form">
                      <label for="newOtherGoal" v-if="newOtherGoals.length<=1">Enter new goal and press enter</label>
                      <p class="input mr-0">
                        <input type="text" name="newOtherGoal" v-validate="'min:10|max:25'" class="category__input form-control" :class="{'is-danger':errors.has('newOtherGoal')}" id="newOtherGoal" v-if="newOtherGoals.length<=1" v-model="newOtherGoalInput" @keyup.enter="addNewOtherGoal($event.target.value)">
                        <span class="help is-danger" v-show="errors.has('newOtherGoal')">{{errors.first('newOtherGoal')}}</span>
                      </p>
                    </div>
                  </div>
                </ul>

                <ul class="roadmap horizontal">
                  <div class="roadmap__bar"></div>
                  <transition-group class="roadmap__items" id="roadmap__horizontal__items" ref="roadmapHorizontal" name="list" enter-active-class="animated fadeInDown" tag="div">
                    <li class="newGoal" v-for="(newGoal,id) in newGoals" :key="id" :title="newGoal.titleFuture">
                      <img :src="$store.state.assetBaseUrl + newGoal.iconPath" alt="" class="newGoal__icon">
                      <p class="newGoal__title">{{newGoal.titleFuture}}</p>
                      <button class="newGoal__remove" @click="removeNewGoal(id,newGoal.catId,newGoal.optId)"><i class="fas fa-times"></i></button>
                    </li>
                  </transition-group>
                  <div class="age-droplet">
                    <div class="droplet"></div>
                    <div class="age-droplet__circle">
                      <b>{{currentAge}}</b>
                      <b>years</b>
                    </div>
                  </div>
                </ul>

                <!-- vertical roadmap -->
                <ul class="roadmap vertical">
                  <div class="roadmap__bar"></div>
                  <transition-group class="roadmap__items" id="roadmap__vertical__items" ref="roadmapVertical" name="list" enter-active-class="animated fadeInLeft" tag="div">
                    <li class="newGoal" v-for="(newGoal,id) in newGoals" :key="id" :title="newGoal.titleFuture">
                      <img :src="$store.state.assetBaseUrl + newGoal.iconPath" alt="" class="newGoal__icon">
                      <p class="newGoal__title">{{newGoal.titleFuture}}</p>
                      <button class="newGoal__remove" @click="removeNewGoal(id,newGoal.catId,newGoal.optId)"><i class="fas fa-times"></i></button>
                    </li>
                  </transition-group>
                  <div class="age-droplet">
                    <div class="droplet"></div>
                    <div class="age-droplet__circle">
                      <p>{{currentAge}} years</p>
                    </div>
                  </div>
                </ul>

                <!-- Go to next step button -->
                <button class="button cta" @click="goAssessRisk" :disabled="!newGoals.length">Assess risks</button>
            </div>
        </div>
        <!-- image preload area -->
        <div class="img-preload" style="display:none">
          <div v-for="(category,categoryId) in categories" :key="categoryId">
            <ul class="category__options">
                <li v-for="(option,optionId) in category.options" :key="optionId">
                  <img :src="option.iconPath" alt="">
                </li>
            </ul>
          </div>
        </div>
        <!-- end image preload area -->
    </div>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  props: ["iconMiscPath"],
  data() {
    return {
      step: 1,
      newGoals: [],
      newOtherGoals: [],
      newOtherGoalInput: ""
    };
  },

  computed: {
    ...mapGetters(["categories", "accomplishments", "currentAge"])
  },

  activated() {
    setTimeout(() => {
      this.$step = 1;
    }, 500);
  },

  mounted() {
    var timeline = document.querySelectorAll(".roadmap.vertical");
    var scrollpos = window.scrollY;
    window.addEventListener("scroll", function() {
      scrollpos = window.scrollY;
      if (scrollpos >= 150) {
        timeline.forEach(element => {
          element.classList.add("active");
        });
      } else {
        timeline.forEach(element => {
          element.classList.remove("active");
        });
      }
    });
  },

  methods: {
    goAssessRisk() {
      this.$store.commit("updateFutureGoals", this.newGoals);
      this.$router.push({
        name: "RiskAssesment"
      });
    },
    selectNewGoal(catId, optionId) {
      var selectedGoal = this.categories[catId].options[optionId];
      selectedGoal.selected = true;
      this.newGoals.push(selectedGoal);
      this.$nextTick().then(() => {
        var verticalTimeline = this.$refs.roadmapVertical.$el;
        var horizontalTimeline = this.$refs.roadmapHorizontal.$el;
        setTimeout(() => {
          verticalTimeline.scrollTop = verticalTimeline.scrollHeight;
          horizontalTimeline.scrollLeft = horizontalTimeline.scrollWidth;
        }, 50);
      });
    },

    removeNewGoal(selfId, catId, optId) {
      this.newGoals.splice(selfId, 1);

      // check if newOtherGoals have any item and
      // catId is equal to the length of categories (which means it was added by 'addNewOtherGoal' function)
      // so that we know it's a category from newOtherGoals
      if (this.newOtherGoals.length && catId === this.categories.length) {
        this.newOtherGoals.map((el, id) => {
          if (el.catId === catId && el.optId === optId) {
            this.newOtherGoals.splice(id, 1);
          }
        });
      } else {
        this.categories[catId].options[optId].selected = false;
      }
    },

    addNewOtherGoal(value) {
      var vm = this;
      var obj = {
        catId: vm.categories.length,
        optId: vm.newOtherGoals.length,
        titleFuture: value,
        selected: false,
        iconPath: vm.iconMiscPath
      };
      this.$validator.validateAll().then(result => {
        if (!result || !vm.newOtherGoalInput.length) {
          return;
        }
        vm.newOtherGoalInput = "";
        vm.newOtherGoals.push(obj);
        vm.newGoals.push(obj);
      });
      this.$nextTick().then(() => {
        var verticalTimeline = this.$refs.roadmapVertical.$el;
        var horizontalTimeline = this.$refs.roadmapHorizontal.$el;
        setTimeout(() => {
          verticalTimeline.scrollTop = verticalTimeline.scrollHeight;
          horizontalTimeline.scrollLeft = horizontalTimeline.scrollWidth;
        }, 50);
      });
    },

    removeNewOtherGoal(selfId, catId, optId) {
      this.newOtherGoals.splice(selfId, 1);
      this.newGoals.map((el, id) => {
        if (el.catId === catId && el.optId === optId) {
          this.newGoals.splice(id, 1);
        }
      });
    }
  }
};
</script>

<style>
</style>



