<template>
    <div id="register" class="register">
        <header class="site-header">
            <a href="" class="site-url"><img src="@/assets/images/logo.png" alt="" class="logo"></a>
        </header>

        <div class="register__main container">
            <p class="register__msg lead">Let’s begin. Tell us a little about yourself. <br>It will help us create your risk assessment.</p>

            <div class="register__form form">
                <div class="form-field" :class="{'done':fields.age && fields.age.dirty}">
                    I'm
                    <p class="input mr-0">
                        <input type="digit" name="age" @keydown.enter.prevent="validate" key="user-age" v-model.number="user.age" v-validate="'required|min_value:12|max_value:99'" data-vv-validate-on="enter|blur" min="12" max="99" class="form-control age" :class="{'is-danger':errors.has('age')}">
                        <span class="help is-danger" v-show="errors.has('age')">{{errors.first('age')}}</span>
                    </p> years old,
                </div>
                <transition enter-active-class="animated fadeInUp">
                    <div class="form-field" v-show="displayGender" :class="{'done':user.gender}">
                        <label for="gender">I'm</label>
                        <div class="form-group gender">
                            <button @click="user.gender = 'female'" :class="{'selected':user.gender=='female'}" type="button">female</button>
                            <button @click="user.gender = 'male'" :class="{'selected':user.gender=='male'}" type="button">male</button>
                        </div>
                    </div>
                </transition>
                <transition enter-active-class="animated fadeInUp">
                    <div class="form-field" v-show="user.gender" :class="{'done':fields.zip && fields.zip.dirty}">
                        My zip code is 
                        <p class="input mr-0">
                            <input type="digit" name="zip" @keydown.enter.prevent="validate" key="user-zip" maxlength="8" v-model.number="user.zip" v-validate="'required|min:5|max:8|numeric'" data-vv-validate-on="blur" class="form-control" :class="{'is-danger':errors.has('zip')}">
                            <span class="help is-danger" v-show="errors.has('zip')">{{errors.first('zip')}}</span>
                        </p>
                    </div>
                </transition>
                <transition enter-active-class="animated fadeInUp">
                    <button v-show="displaySignUp" class="button cta site-entry-cta" @click="onSubmit" :disabled="signingUp=='loading'">
                      <span v-show="signingUp==''">Sign up</span>
                      <span v-show="signingUp=='loading'">Signing up ...</span>
                      <span v-show="signingUp=='success'">Signed up !</span>
                    </button>
                </transition>
            </div>
        </div>
        <!-- end register main -->
        <div class="register__timeline"></div>
    </div>
</template>

<script>
export default {
  data() {
    return {
      signingUp: "",
      showGender: false,
      showSignUp: false,
      user: {
        age: "",
        gender: "",
        zip: ""
      }
    };
  },
  methods: {
    validate() {
      this.$validator.validateAll();
    },
    onSubmit() {
      var vm = this;
      vm.signingUp = "loading";
      this.$validator.validateAll().then(result => {
        if (!result) {
          return;
        }
        this.$store.commit("updateCurrentAge", this.user.age);
        this.$router.push({
          name: "Accomplishments"
        });
      });
    }
  },
  computed: {
    displayGender() {
      if (this.fields.age && this.fields.age.valid) {
        this.showGender = true;
      }
      return this.showGender;
    },
    displaySignUp() {
      if (this.fields.zip && this.fields.zip.valid) {
        this.showSignUp = true;
      }
      return this.showSignUp;
    }
  }
};
</script>

<style>
</style>



