<template>
    <div class="savePlan" id="savePlan">
        <header class="site-header">
            <a href="" class="site-url"><img src="@/assets/images/logo.png" alt="" class="logo"></a>
          <ul class="progress">
              <div class="progress-bar" :style="{width:$step*25+'%'}"></div>
              <div class="progress-bar-base"></div>
              <li class="progress-step" :class="{active:step==0,done:step>0}">
                <span class="progress-step-serial">1
                  <span class="progress-step-name">Accomplishments</span>
                </span>
                
              </li>
              <li class="progress-step" :class="{active:step==1,done:step>1}">
                <span class="progress-step-serial">2
                  <span class="progress-step-name">Set future goals</span>
                </span>
                
              </li>
              <li class="progress-step" :class="{active:step==2,done:step>2}">
                <span class="progress-step-serial">3
                  <span class="progress-step-name">Assess risks</span>
                </span>
                
              </li>
              <li class="progress-step" :class="{active:step==3,done:step>3}">
                <span class="progress-step-serial">4
                  <span class="progress-step-name">Save my plan</span>
                </span>
                
              </li>
            </ul>
        </header>

        <div class="savePlan__main container">
            <p class="savePlan__msg lead">Great! Way to go</p>
            <p class="savePlan__instruction">You have a wonderful plan there, but you may wish to pay attention to the following areas:</p>
            <ul class="risks__list d-none d-lg-block">
              <li>Risk of short term disability</li>
              <li>Risk of critical illness</li>
            </ul>

            <div class="savePlan__accordion">
                <ul class="accordion risks">
                    <li class="accordion__item risk" v-for="(risk,id) in risks" :key="id">
                        <div class="accordion__title" @click="risk.selected=!risk.selected">
                            <h3>{{risk.title}}</h3>
                            <span v-show="risk.selected"><i class="fas fa-angle-up"></i></span>
                            <span v-show="!risk.selected"><i class="fas fa-angle-down"></i></span>
                        </div>
                        <div class="accordion__body">
                            <div class="risk__details" :class="{'active':risk.selected}">
                                <div v-html="risk.body"></div>
                                <div class="risk__help">
                                    <a href="#"><i class="fas fa-shield-alt"></i> How can I protect myself ?</a>
                                    <a href="#"><i class="fas fa-question-circle"></i> How are risks calculated ?</a>
                                </div>
                            </div>
                        </div>
                        <button class="accordion__toggler" @click="risk.selected=!risk.selected">
                            <span v-show="risk.selected">Hide Details</span>
                            <span v-show="!risk.selected">Show Details</span>
                        </button>
                    </li>
                </ul>
            </div>
            <!-- end savePlan accordion -->
        </div>

        <div class="savePlan__timeline d-none d-lg-block">
          <ul class="roadmap horizontal">
            <div class="roadmap__bar"></div>
            <div class="roadmap__accomplishment">
              <li class="roadmap__item goal" v-for="(accomplishment,id) in accomplishments" :key="id">
                <img :src="assetBaseUrl + accomplishment.iconPath" alt="" class="roadmap__item__icon" title="Rented a home">
              </li>
              <li class="roadmap__item risk">
                <img src="/static/images/game/curve/curve50-75.png" alt="" class="roadmap__item__curve">
                <button class="button" @click="slideTo(0)">36.7% short term disability</button>
              </li>
              <li class="roadmap__item risk">
                <img src="/static/images/game/curve/curve50-75.png" alt="" class="roadmap__item__curve">
                <button class="button" @click="slideTo(0)">36.7% short term disability</button>
              </li>
            </div>
            <div class="age-droplet currentAge">
              <div class="droplet"></div>
              <div class="age-droplet__circle">
                <b>{{currentAge}}</b>
                <b>years</b>
              </div>
            </div>
            <div class="roadmap__futureGoals">
              <li class="roadmap__item goal" v-for="(goal,id) in futureGoals" :key="id">
                <img :src="assetBaseUrl + goal.iconPath" alt="" class="roadmap__item__icon" title="Chose a partner">
              </li>
              <li class="roadmap__item risk">
                <img src="/static/images/game/curve/curve50-75.png" alt="" class="roadmap__item__curve">
                <button class="button" @click="slideTo(1)">58.1% critical illness</button>
              </li>
            </div>
            <div class="age-droplet retireAge">
              <div class="droplet"></div>
              <div class="age-droplet__circle">
                <b>{{retireAge}}</b>
                <b>years</b>
              </div>
            </div>
          </ul>
        </div>

        <div class="savePlan__swiper risk__carousel">
            <swiper :options="swiperOption" ref="savePlanSwiper">
              <!-- slides -->
              <swiper-slide class="savePlan__swiper__item">
                <h3 class="risk__carousel__title">Short Term Disability</h3>
                <h4 class="risk__carousel__msg">36.7%  of people like you (based on the info you provided) are likely to take 1 month or more off work due to a physical injury or illness.</h4>
                <div class="risk__carousel__main row">
                  <div class="risk__carousel__body col-md-9 col-sm-12">
                    <p>Many injuries like concussions, fractures, or complications from pregnancy, require a few weeks or months off work.</p>
                    <p>It’s hard to focus on recovery, when bills are piling up and you can’t work for your paycheck.</p>
                    <p>If you are unable to work due to an illness, injury, or (for women) pregnancy, Short Term Disability insurance may pay you a portion of your income for 3-6 months.</p>
                  </div>
                  <div class="risk__carousel__help col-md-3 col-sm-12">
                    <a href="#"><i class="fas fa-shield-alt"></i> How can I protect myself ?</a>
                    <a href="#"><i class="fas fa-question-circle"></i> How are risks calculated ?</a>
                  </div>
                </div>
              </swiper-slide>
              <swiper-slide class="savePlan__swiper__item">
                <h3 class="risk__carousel__title">Critical Illness</h3>
                <h4 class="risk__carousel__msg">58.1%  of people like you (based on the demographic information you provided) are likely to be diagnosed with a critical illness (e.g. cancer, heart attack, stroke, coronary artery disease) before retirement.</h4>
                <div class="risk__carousel__main row">
                  <div class="risk__carousel__body col-md-8 col-sm-12">
                    <p>Illnesses like this often result in long-term costs—such as rehabilitation and medical tests—beyond what health insurance and Medicare cover.</p>
                    <p>Critical Illness Insurance pays you a lump sum to use however you want. It can help you avoid financial strain from lost income, co-pays, deductibles and the therapies & treatments not covered by health insurance.</p>
                  </div>
                  <div class="risk__carousel__help col-md-4 col-sm-12">
                    <a href="#"><i class="fas fa-shield-alt"></i> How can I protect myself ?</a>
                    <a href="#"><i class="fas fa-question-circle"></i> How are risks calculated ?</a>
                  </div>
                </div>
              </swiper-slide>
              <!-- Optional controls -->
              <!-- <div class="swiper-pagination"  slot="pagination"></div> -->
              <div class="swiper-button-prev" slot="button-prev"></div>
              <div class="swiper-button-next" slot="button-next"></div>
              <!-- <div class="swiper-scrollbar"   slot="scrollbar"></div> -->
            </swiper>
        </div>
        
        <transition name="modal">
          <modal name="foo" width="80%" height="auto" :scrollable="true" :adaptive="true" transition="nice-modal-fade">
            <div slot="top-right">
              <button @click="$modal.hide('foo')" class="close-modal">
                <i class="fas fa-times"></i>
              </button>
            </div>
            Hello, ☀️!
          </modal>
        </transition>
        <!-- <button @click="$modal.show('foo')">
          show modal
        </button> -->
        <div class="savePlan__footer" :class="{'active':openFooter}">
          <p class="mr-0 savePlan__footer__action" @click="openFooter = !openFooter">
            Click here to protect your plan
            <span v-show="openFooter"><i class="fas fa-angle-down"></i></span>
            <span v-show="!openFooter"><i class="fas fa-angle-up"></i></span>
          </p>
          <div class="btn-group">
            <button class="savePlan__footer__cta button">Email me my risk assessment</button>
            <button class="savePlan__footer__cta button">Remind me when I can change my coverage</button>
            <button class="savePlan__footer__cta button">Tell me how to change my coverage</button>
          </div>
        </div>
    </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      step: 3,
      accessToken: null,
      getToken: {
        grant_type: "password",
        client_id: "client_api",
        client_secret: "rRfbeBhGF53a5Nq6aqMaKD3EWuPPyPG2",
        username: "admin",
        password: "go_life"
      },

      swiperOption: {
        slidesPerView: 1,
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev"
        }
      },
      openFooter: false,
      risks: [
        {
          title: "Risk of short term disability",
          name: "Short Term Disability",
          body: `<p>36.7%  of people like you (based on the info you provided) are likely to take 1 month or more off work due to a physical injury or illness.</p>
                  <p>Many injuries like concussions, fractures, or complications from pregnancy, require a few weeks or months off work.</p>
                <p>It’s hard to focus on recovery, when bills are piling up and you can’t work for your paycheck.</p>
                <p>If you are unable to work due to an illness, injury, or (for women) pregnancy, Short Term Disability insurance may pay you a portion of your income for 3-6 months.</p>`,
          selected: false
        },
        {
          title: "Risk of critical illness",
          name: "Critical Illness",
          body: `<p>10.7% of people like you (based on the demographic information you provided) are likely to be diagnosed with a critical illness (e.g. cancer, heart attack, stroke, coronary artery disease) before retirement.</p>
          <p>Illnesses like this often result in long-term costs—such as rehabilitation and medical tests—beyond what health insurance and Medicare cover.</p>
                <p>Critical Illness Insurance pays you a lump sum to use however you want. It can help you avoid financial strain from lost income, co-pays, deductibles and the therapies & treatments not covered by health insurance.</p>`,
          selected: false
        }
      ]
    };
  },
  activated() {
    setTimeout(() => {
      this.$step = 3;
    }, 500);
  },
  computed: {
    ...mapGetters([
      "assetBaseUrl",
      "accomplishments",
      "futureGoals",
      "currentAge",
      "retireAge"
    ]),
    swiper() {
      return this.$refs.savePlanSwiper.swiper;
    }
  },
  methods: {
    slideTo(id) {
      this.swiper.slideTo(id, 1000, false);
    }
  },
  created() {
    this.$axios
      .post("http://18.216.50.166:1350/oauth/token", this.getToken)
      .then(res => {
        this.accessToken = res.data.access_token;
      })
      .then(() => {
        this.$axios
          .get(
            "http://18.216.50.166:1350/calc?age=35&gender=M&height_feet=5&height_inches=10&weight=200&salary=75000&retirement_age=67&medical_treatment=N&occupation_name=Officers and Managers&industry_name=Retail Trade&zip_code=07960&access_token=" +
              this.accessToken
          )
          .then(res => {
            console.log(res.data);
          });
      })
      .catch(err => {
        console.log(err);
      });
  }
};
</script>

<style>
</style>



