<template>
    <div id="lifeQuize" class="lifeQuize">
        <transition enter-active-class="animated fadeIn" leave-active-class="animated fadeOut">
          <div class="page-loader" v-show="loading">
            <clip-loader :loading="true" color="#007ABC"></clip-loader>
          </div>
        </transition>
        <header class="site-header">
            <a href="" class="site-url"><img src="@/assets/images/logo.png" alt="" class="logo"></a>
            <ul class="progress">
              <div class="progress-bar" :style="{width:$step*25+'%'}"></div>
              <div class="progress-bar-base"></div>
              <li class="progress-step" :class="{active:step==0,done:step>0}">
                <span class="progress-step-serial">1
                  <span class="progress-step-name">Accomplishments</span>
                </span>
                
              </li>
              <li class="progress-step" :class="{active:step==1,done:step>1}">
                <span class="progress-step-serial">2
                  <span class="progress-step-name">Set future goals</span>
                </span>
                
              </li>
              <li class="progress-step" :class="{active:step==2,done:step>2}">
                <span class="progress-step-serial">3
                  <span class="progress-step-name">Assess risks</span>
                </span>
                
              </li>
              <li class="progress-step" :class="{active:step==3,done:step>3}">
                <span class="progress-step-serial">4
                  <span class="progress-step-name">Save my plan</span>
                </span>
                
              </li>
            </ul>
        </header>

        <div class="lifeQuize__main">
            <div class="accomplishments" id="accomplishments">
                <h2 class="message">{{headerText}}</h2>
                <p class="direction">{{subHeaderText}}</p>
                <ul class="categories container-fluid">
                  <div v-for="(category,categoryId) in categories" :key="categoryId" class="category col-xs-12 col-sm-6 col-lg-3 col-xl-3" :class="category.class">
                    <p class="category__title">{{category.name}}</p>
                    <ul class="category__options">
                        <li v-for="(option,optionId) in category.options" :key="optionId" class="category__option" @click="selectAccomplishment(categoryId,optionId)" :disabled="option.selected">
                          <span>{{option.titlePast}}</span>
                        </li>
                    </ul>
                  </div>
                  <div class="category addNew col-xs-12 col-sm-6 col-lg-3 col-xl-3">
                    <p class="category__title">Add new accomplishment</p>
                    <div class="newAccomplishments">
                      <p v-for="(accomplishment,id) in newAccomplishments" :key="id" class="newAccomplishment">
                        {{accomplishment.titlePast}}
                        <button @click="removeNewAccomplishment(id,accomplishment.catId,accomplishment.optId)"><i class="fas fa-times"></i></button>
                      </p>
                    </div>
                    <div class="form">
                      <label for="newAccomplishment" v-if="newAccomplishments.length<=1">Enter new accomplishment and press enter</label>
                      <p class="input mr-0">
                        <input type="text" name="newAccomplishment" v-validate="'min:10|max:25'" class="category__input form-control" :class="{'is-danger':errors.has('newAccomplishment')}" id="newAccomplishment" v-if="newAccomplishments.length<=1" v-model="newAccomplishmentInput" @keyup.enter="addNewAccomplishment($event.target.value)">
                        <span class="help is-danger" v-show="errors.has('newAccomplishment')">{{errors.first('newAccomplishment')}}</span>
                      </p>
                    </div>
                  </div>
                </ul>

                <ul class="roadmap horizontal">
                  <div class="roadmap__bar"></div>
                  <transition-group class="roadmap__items" id="roadmap__horizontal__items" ref="roadmapHorizontal" name="list" enter-active-class="animated fadeInDown" tag="div">
                    <li class="accomplishment" v-for="(accomplishment,id) in accomplishments" :key="id" :title="accomplishment.titlePast">
                      <img :src="$store.state.assetBaseUrl + accomplishment.iconPath" alt="" class="accomplishment__icon">
                      <p class="accomplishment__title">{{accomplishment.titlePast}}</p>
                      <button class="accomplishment__remove" @click="removeAccomplishment(id,accomplishment.catId,accomplishment.optId)"><i class="fas fa-times"></i></button>
                    </li>
                  </transition-group>
                  <div class="age-droplet">
                    <div class="droplet"></div>
                    <div class="age-droplet__circle">
                      <b>{{currentAge}}</b>
                      <b>years</b>
                    </div>
                  </div>
                </ul>

                <!-- vertical roadmap -->
                <ul class="roadmap vertical">
                  <div class="roadmap__bar"></div>
                  <transition-group class="roadmap__items" id="roadmap__vertical__items" ref="roadmapVertical" name="list" enter-active-class="animated fadeInLeft" tag="div">
                    <li class="accomplishment" v-for="(accomplishment,id) in accomplishments" :key="id" :title="accomplishment.titlePast">
                      <img :src="$store.state.assetBaseUrl + accomplishment.iconPath" alt="" class="accomplishment__icon">
                      <p class="accomplishment__title">{{accomplishment.titlePast}}</p>
                      <button class="accomplishment__remove" @click="removeAccomplishment(id,accomplishment.catId,accomplishment.optId)"><i class="fas fa-times"></i></button>
                    </li>
                  </transition-group>
                  <div class="age-droplet">
                    <div class="droplet"></div>
                    <div class="age-droplet__circle">
                      <p>{{currentAge}} years</p>
                    </div>
                  </div>
                </ul>

                <!-- Go to next step button -->
                <button class="button cta" @click="goFutureGoals" :disabled="!accomplishments.length">{{nextStepText}}</button>
            </div>
        </div>

        <!-- image preload area -->
        <div class="img-preload" style="display:none">
          <div v-for="(category,categoryId) in categories" :key="categoryId">
            <ul class="category__options">
                <li v-for="(option,optionId) in category.options" :key="optionId">
                  <img :src="$store.state.assetBaseUrl + option.iconPath" alt="">
                </li>
            </ul>
          </div>
        </div>
        <!-- end image preload area -->
    </div>
</template>

<script>
import { TweenMax } from "greensock";
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      step: 0,
      loading: true,
      headerText: "",
      subHeaderText: "",
      nextStepText: "",
      accomplishments: [],
      newAccomplishments: [],
      newAccomplishmentInput: "",
      iconMiscPath: ""
    };
  },

  computed: {
    ...mapGetters(["categories", "currentAge"])
  },

  activated() {
    setTimeout(() => {
      this.$step = 0;
    }, 500);
  },

  created() {
    var name = this.$route.params.name || "default";
    console.log(name);
    this.$axios
      .get(this.$store.state.apiBaseUrl + "rawInfo/" + name)
      .then(res => {
        console.log(res);
        this.iconMiscPath = res.data.genericIconMisc;
        this.headerText = res.data.accomplishment.headerText;
        this.subHeaderText = res.data.accomplishment.subHeaderText;
        this.nextStepText = res.data.accomplishment.buttonText;
        this.loading = false;
      })
      .catch(err => {
        console.log(err);
      });
  },

  mounted() {
    var timeline = document.querySelectorAll(".roadmap.vertical");
    var scrollpos = window.scrollY;
    window.addEventListener("scroll", function() {
      scrollpos = window.scrollY;
      if (scrollpos >= 150) {
        timeline.forEach(element => {
          element.classList.add("active");
        });
      } else {
        timeline.forEach(element => {
          element.classList.remove("active");
        });
      }
    });
  },

  methods: {
    selectAccomplishment(catId, optionId) {
      var selectedAccomplishment = this.categories[catId].options[optionId];
      selectedAccomplishment.selected = true;
      this.accomplishments.push(selectedAccomplishment);
      this.$nextTick().then(() => {
        var verticalTimeline = this.$refs.roadmapVertical.$el;
        var horizontalTimeline = this.$refs.roadmapHorizontal.$el;

        setTimeout(() => {
          verticalTimeline.scrollTop = verticalTimeline.scrollHeight;
          horizontalTimeline.scrollLeft = horizontalTimeline.scrollWidth;
        }, 50);
      });
    },

    removeAccomplishment(selfId, catId, optId) {
      this.accomplishments.splice(selfId, 1);

      // check if newAccomplishments have any item and
      // catId is equal to the length of categories (which means it was added by 'addNewAccomplishment' function)
      // so that we know it's a category from newAccomplishments
      if (this.newAccomplishments.length && catId === this.categories.length) {
        this.newAccomplishments.map((el, id) => {
          if (el.catId === catId && el.optId === optId) {
            this.newAccomplishments.splice(id, 1);
          }
        });
      } else {
        this.categories[catId].options[optId].selected = false;
      }
    },

    addNewAccomplishment(value) {
      var vm = this;
      var obj = {
        catId: vm.categories.length,
        optId: vm.newAccomplishments.length,
        titlePast: value,
        selected: false,
        iconPath: vm.iconMiscPath
      };
      this.$validator.validateAll().then(result => {
        if (!result || !vm.newAccomplishmentInput.length) {
          return;
        }
        vm.newAccomplishmentInput = "";
        vm.newAccomplishments.push(obj);
        vm.accomplishments.push(obj);
      });
      this.$nextTick().then(() => {
        var verticalTimeline = this.$refs.roadmapVertical.$el;
        var horizontalTimeline = this.$refs.roadmapHorizontal.$el;
        setTimeout(() => {
          verticalTimeline.scrollTop = verticalTimeline.scrollHeight;
          horizontalTimeline.scrollLeft = horizontalTimeline.scrollWidth;
        }, 50);
      });
    },

    removeNewAccomplishment(selfId, catId, optId) {
      this.newAccomplishments.splice(selfId, 1);
      this.accomplishments.map((el, id) => {
        if (el.catId === catId && el.optId === optId) {
          this.accomplishments.splice(id, 1);
        }
      });
    },

    goFutureGoals() {
      this.$store.commit("updateCategories", this.categories);
      this.$store.commit("updateAccomplishments", this.accomplishments);
      this.$router.push({
        name: "FutureGoals",
        params: {
          iconMiscPath: this.iconMiscPath,
          userId: this.userId,
          accomplishments: this.accomplishments
        }
      });
    }
  }
};
</script>

<style>
</style>



