<template>
    <div id="home" class="home">
        <transition enter-active-class="animated fadeIn" leave-active-class="animated fadeOut">
          <div class="page-loader" v-show="loading">
            <clip-loader :loading="true" color="#007ABC"></clip-loader>
          </div>
        </transition>
        <div class="home__container container">
            <div class="site-intro">
                <h2 class="site-title">{{titleText}}</h2>
                <img src="@/assets/images/logo.png" alt="lifenavigator logo" class="site-logo">
                <p class="site-tagline">{{taglineText}}</p>
            </div>

            <div class="site-entry">
                <p class="site-description">{{descriptionText}}</p>
                <!-- <button class="site-moreAbout link" v-tooltip.auto="findTooltip">Find out more.</button> -->
                <popover :always="false" :width="350">
                    <button class="site-moreAbout link">{{tooltipTitle}}</button>
                    <div slot="content">
                        <p>Risk probabilities are calculated based on data from the Centers for Disease Control & Prevention (the leading national public health institute in the U.S.), the National Safety Council Injury Facts, and 2017 actuary tables.We will not ask you for any information that would identify you (such as name, address, or phone number). Life Navigator is simply a way for you to learn more about risks you might be facing.</p>
                    </div>
                </popover>
                
                <button class="button cta site-entry-cta" @click="onBeginClick">{{beginBtnText}}</button>
            </div>
        </div>
        <!-- end home container -->
        <div class="home-timeline">
            <img src="@/assets/images/welcome-roadmap.png" alt="">
        </div>
    </div>
</template>

<script>
export default {
  data() {
    return {
      loading: true,
      titleText: "",
      logoPath: "",
      taglineText: "",
      descriptionText: "",
      tooltipTitle: "",
      beginBtnText: ""
    };
  },
  methods: {
    onBeginClick() {
      this.$router.push({
        name: "Register"
      });
    }
  },
  created() {
    var name = this.$route.params.name || "default";
    console.log(name);
    this.$axios
      .get(this.$store.state.apiBaseUrl + "rawInfo/" + name)
      .then(res => {
        console.log(res);
        this.$store.commit("updateCategories", res.data.categories);
        var homeData = res.data.home;
        this.titleText = homeData.welComeText;
        this.logoPath = homeData.logoPath;
        this.taglineText = homeData.header;
        this.descriptionText = homeData.info;
        this.tooltipTitle = homeData.anchorText;
        this.beginBtnText = homeData.buttonText;
        this.loading = false;
      })
      .catch(err => {
        console.log(err);
      });
  }
};
</script>

<style>
</style>



