<template>
    <div class="riskAssessment" id="riskAssessment">
        <transition enter-active-class="animated fadeIn" leave-active-class="animated fadeOut">
          <div class="page-loader" v-show="loading">
            <clip-loader :loading="true" color="#007ABC"></clip-loader>
          </div>
        </transition>
        <header class="site-header">
            <a href="" class="site-url"><img src="@/assets/images/logo.png" alt="" class="logo"></a>
            <ul class="progress">
              <div class="progress-bar" :style="{width:$step*25+'%'}"></div>
              <div class="progress-bar-base"></div>
              <li class="progress-step" :class="{active:step==0,done:step>0}">
                <span class="progress-step-serial">1
                  <span class="progress-step-name">Accomplishments</span>
                </span>
                
              </li>
              <li class="progress-step" :class="{active:step==1,done:step>1}">
                <span class="progress-step-serial">2
                  <span class="progress-step-name">Set future goals</span>
                </span>
                
              </li>
              <li class="progress-step" :class="{active:step==2,done:step>2}">
                <span class="progress-step-serial">3
                  <span class="progress-step-name">Assess risks</span>
                </span>
                
              </li>
              <li class="progress-step" :class="{active:step==3,done:step>3}">
                <span class="progress-step-serial">4
                  <span class="progress-step-name">Save my plan</span>
                </span>
                
              </li>
            </ul>
        </header>

        <div class="riskAssessment__main container">
            <p class="riskAssessment__msg lead">{{headerText}}</p>
            <p class="riskAssessment__instruction">{{paragraphText}}<br><br>{{subHeaderText}}</p>

            <div class="riskAssessment__form form">
                <div class="form-field" :class="{'done':(fields.occupation && fields.occupation.dirty) && (fields.industry && fields.industry.dirty)}">
                    I work as a 
                    <p class="select">
                        <select name="occupation" id="" v-model="user.occupation" v-validate="'required'" class="form-control occupation" :class="{'is-danger':errors.has('occupation')}">
                            <option value="">Occupation</option>
                            <option value="Officers and Managers">Officers and Manager</option>
                            <option value="Office and Clerical">Office and Clerical</option>
                            <option value="Professional">Professional</option>
                            <option value="Technicians, Non-office Managerial">Technician; Non-office Managerial</option>
                            <option value="Skilled Crafts and Laborers">Skilled Crafts and Laborers</option>
                            <option value="Semi-Skilled Laborers">Semi-Skilled Laborers</option>
                            <option value="Unskilled Laborers, Hazardous Occupations">Unskilled Laborer; Hazardous Occupation</option>
                            <option value="UAW/Assembly Line">UAW/Assembly Line</option>
                        </select>
                        <span class="help is-danger" v-show="errors.has('occupation')">{{errors.first('occupation')}}</span>
                    </p>
                    in
                    <p class="select">
                        <select name="industry" id="" v-model="user.industry" v-validate="'required'" class="form-control industry" :class="{'is-danger':errors.has('industry')}">
                            <option value="">Industry</option>
                            <option value="Agriculture, Forestry and Fishing">Agriculture, Forestry, &amp; Fishing</option>
                            <option value="Construction">Construction</option>
                            <option value="Finance, Insurance and Real Estate">Finance, Insurance &amp; Real Estate</option>
                            <option value="Mining">Mining</option>
                            <option value="Manufacturing">Manufacturing</option>
                            <option value="Public Administration">Public Administration</option>
                            <option value="Retail Trade">Retail Trade</option>
                            <option value="Services">Services</option>
                            <option value="Transportation, Communications, Electric, Gas and Sanitary Services">Transportation, Communications, Electric, Gas &amp; Sanitary Services</option>
                            <option value="Wholesale Trade">Wholesale Trade</option>
                        </select>
                        <span class="help is-danger" v-show="errors.has('industry')">{{errors.first('industry')}}</span>
                    </p>
                </div>
                <transition enter-active-class="animated fadeInUp">
                    <div class="form-field" v-show="displayIncome" :class="{'done':user.income}">
                        I make $ 
                        <p class="input">
                            <input type="digit" name="income" size="8" maxlength="8" v-model.number="user.income" v-validate="'required|numeric|min:5|max:8'" class="form-control income" :class="{'is-danger':errors.has('income')}">
                            <span class="help is-danger" v-show="errors.has('income')">{{errors.first('income')}}</span>
                        </p>
                    </div>
                </transition>
                <transition enter-active-class="animated fadeInUp">
                    <div class="form-field" v-show="displayHeightnWeight" :class="{'done':(fields.feet && fields.feet.dirty) && ((fields.inch && fields.inch.dirty)) && (fields.weight && fields.weight.dirty)}">
                        I am 
                        <p class="input">
                            <input type="digit" name="feet" v-model.number="user.tallInFeet" v-validate="'required|numeric'" size="1" maxlength="1" class="form-control tallInFeet" :class="{'is-danger':errors.has('feet')}">
                            <span class="help is-danger" v-show="errors.has('feet')">{{errors.first('feet')}}</span>
                        </p> feet and 
                        <p class="input">
                            <input type="digit" name="inch" v-model.number="user.tallInInch" v-validate="'required|numeric|between:0,11'" size="2" maxlength="2" class="form-control tallInInch" :class="{'is-danger':errors.has('inch')}">
                            <span class="help is-danger" v-show="errors.has('inch')">{{errors.first('inch')}}</span>
                        </p> inches tall, and I weigh 
                        <p class="input">
                            <input type="digit" name="weight" v-model.number="user.weight" v-validate="'required|numeric'" size="3" maxlength="3" class="form-control weight" :class="{'is-danger':errors.has('weight')}">
                            <span class="help is-danger" v-show="errors.has('weight')">{{errors.first('weight')}}</span>
                        </p> pounds
                    </div>
                </transition>
                <transition enter-active-class="animated fadeInUp">
                    <div class="form-field" v-show="displaySmoking" :class="{'done':user.doesSmoke}">
                        I 
                        <div class="form-group smoker">
                            <button @click="user.doesSmoke='yes'" type="button" :class="{'selected':user.doesSmoke=='yes'}">do</button>
                            <button @click="user.doesSmoke='no'" type="button" :class="{'selected':user.doesSmoke=='no'}">don't</button>
                        </div> smoke, and
                    </div>
                </transition>
                <transition enter-active-class="animated fadeInUp">
                    <div class="form-field" v-show="user.doesSmoke" :class="{'done':fields.illness && fields.illness.dirty}">
                        in the past 5 years, I have had 
                        <p class="select">
                            <select name="illness" id="" v-model="user.pastIllness" v-validate="'required'" class="form-control illness" :class="{'is-danger':errors.has('illness')}">
                                <option value="">Select</option>
                                <option value="No medical condition">No medical condition</option>
                                <option value="Alzheimers">Alzheimers</option>
                                <option value="CABG">CABG</option>
                                <option value="Cancer">Cancer</option>
                                <option value="ESR">ESR</option>
                                <option value="A heart attack">A heart attack</option>
                                <option value="In-situ cancer">In-situ cancer</option>
                                <option value="MOT">MOT</option>
                                <option value="Skin cancer">Skin cancer</option>
                                <option value="A stroke">A stroke</option>
                                <option value="Other">Other</option>
                            </select>
                            <span class="help is-danger" v-show="errors.has('illness')">{{errors.first('illness')}}</span>
                        </p>
                    </div>
                </transition>
                <transition enter-active-class="animated fadeInUp">
                    <div class="form-field" v-show="displayRetire" :class="{'done':fields.retireAge && fields.retireAge.dirty }">
                        I plan to retire at the age of 
                        <p class="input">
                            <input type="digit" name="retireAge" size="2" maxlength="2" v-model.number="user.retireAge" v-validate="'required|min:2|max:2'" class="form-control retirement" :class="{'is-danger':errors.has('retireAge')}">
                            <span class="help is-danger" v-show="errors.has('retireAge')">{{errors.first('retireAge')}}</span>
                        </p>
                    </div>
                </transition>
                <transition enter-active-class="animated fadeInUp">
                    <button @click="onSubmit" v-show="displayNext" class="button cta" to="/game/step-4">Next</button>
                </transition>
            </div>
        </div>
        <!-- end riskAssessment main -->
        <div class="riskAssessment__timeline"></div>
    </div>
</template>

<script>
export default {
  data() {
    return {
      step: 2,
      loading: true,
      headerText: "",
      subHeaderText: "",
      paragraphText: "",
      showIncome: false,
      showHeightnWeight: false,
      showSmoking: false,
      showRetire: false,
      showNext: false,
      user: {
        occupation: "",
        industry: "",
        income: "",
        tallInFeet: "",
        tallInInch: "",
        weight: "",
        doesSmoke: "",
        pastIllness: "",
        retireAge: ""
      }
    };
  },
  created() {
    var name = this.$route.params.name || "default";
    console.log(name);
    this.$axios
      .get(this.$store.state.apiBaseUrl + "rawInfo/" + name)
      .then(res => {
        console.log(res);
        var riskData = res.data.assessRisk;
        this.headerText = riskData.headerText;
        this.subHeaderText = riskData.subHeaderText;
        this.paragraphText = riskData.paragraphText;
        this.loading = false;
      })
      .catch(err => {
        console.log(err);
      });
  },
  methods: {
    onSubmit() {
      this.$validator.validateAll().then(result => {
        if (!result) {
          return;
        }
        this.$store.commit("updateRetireAge", this.user.retireAge);
        this.$router.push({
          name: "SavePlan"
        });
      });
    }
  },
  activated() {
    setTimeout(() => {
      this.$step = 2;
    }, 500);
  },
  computed: {
    displayIncome() {
      if (
        this.fields.occupation &&
        this.fields.occupation.valid &&
        (this.fields.industry && this.fields.industry.valid)
      ) {
        this.showIncome = true;
      }
      return this.showIncome;
    },
    displayHeightnWeight() {
      if (this.fields.income && this.fields.income.valid) {
        this.showHeightnWeight = true;
      }
      return this.showHeightnWeight;
    },
    displaySmoking() {
      if (
        this.fields.feet &&
        this.fields.feet.valid &&
        (this.fields.inch && this.fields.inch.valid) &&
        (this.fields.weight && this.fields.weight.valid)
      ) {
        this.showSmoking = true;
      }
      return this.showSmoking;
    },
    displayRetire() {
      if (
        this.user.doesSmoke &&
        this.fields.illness &&
        this.fields.illness.valid
      ) {
        this.showRetire = true;
      }
      return this.showRetire;
    },
    displayNext() {
      if (this.fields.retireAge && this.fields.retireAge.valid) {
        this.showNext = true;
      }
      return this.showNext;
    }
  }
};
</script>

<style>
</style>



