import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    apiBaseUrl: 'http://18.216.50.166:3000/api/',
    assetBaseUrl: 'http://18.216.50.166:3000/',
    currentAge: null,
    retireAge: null,
    categories: null,
    accomplishments: null,
    futureGoals: null
  },
  getters: {
    apiBaseUrl: state => {
      return state.apiBaseUrl;
    },
    assetBaseUrl: state => {
      return state.assetBaseUrl;
    },
    currentAge: state => {
      return state.currentAge;
    },
    retireAge: state => {
      return state.retireAge;
    },
    categories: state => {
      return state.categories;
    },
    accomplishments: state => {
      return state.accomplishments;
    },
    futureGoals: state => {
      return state.futureGoals;
    }
  },
  mutations: {
    updateCategories(state, payload) {
      state.categories = payload;
    },
    updateAccomplishments(state, payload) {
      state.accomplishments = payload;
    },
    updateFutureGoals(state, payload) {
      state.futureGoals = payload;
    },
    updateCurrentAge(state, payload) {
      state.currentAge = payload;
    },
    updateRetireAge(state, payload) {
      state.retireAge = payload;
    }
  },
  actions: {

  }
})



